<template>
<div class="pos">
    <el-row class="pos-row">
        <el-col :span="6" class="pos-order">
            <el-tabs>
                <el-tab-pane label="点餐">
                    <h1>{{deskInfo}}</h1>
                    <el-button type="primary" icon="el-icon-delete" size="small" v-if="isClear" @click="clearDesk(deskID)">清理</el-button>
                    <el-table :data="tableData" border show-summary style="width: 100%;height:100%">
                        <el-table-column prop="name" label="商品" width="80"></el-table-column>
                        <el-table-column prop="count" label="数量" width="50"></el-table-column>
                        <el-table-column prop="price" label="单价" width="60"></el-table-column>
                        <el-table-column label="操作" width="150">
                            <template slot-scope="scope">
                                <el-button type="success" size="mini" @click="delOrderList(scope.row)">删除</el-button>
                                <el-button type="success" size="mini" @click="addOrderList(scope.row)">增加</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>

                <el-tab-pane label="挂单">
                    <el-table :data="noPayData" border show-summary style="width: 100%;height:100%">
                        <el-table-column prop="tableId" label="桌号"></el-table-column>
                        <el-table-column prop="payPrice" label="消费" width="50"></el-table-column>
                        <el-table-column prop="tableId" label="操作">
                            <template slot-scope="scope">
                                <el-button type="success" size="small" @click="checkoutFinal(scope.row)">结账</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>

                <el-tab-pane label="外卖">外卖</el-tab-pane>
            </el-tabs>
            <el-input type="text" placeholder="请输入会员号" v-model="input" maxlength="11" show-word-limit>
            </el-input>
            <el-button type="info" @click="checkVip(input)" icon="el-icon-search"></el-button>
            <el-button type="warning" @click="addNoPayData(deskID,allPrice)">挂单</el-button>
            <el-button type="danger" @click="tableData = []">删除</el-button>
            <el-button type="success" @click="checkoutFinal">结账</el-button>
        </el-col>

        <el-col :span="18">
            <div class="often-goods">
                <div class="title">常用商品</div>
                <div class="often-goods-list">
                    <ul>
                        <li v-for="goods in oftenGoods" :key="goods.goodsId" @click="addOrderList(goods)"><span>{{goods.goodsName}}</span><span class="o-price">￥{{goods.price}}元</span></li>
                    </ul>
                </div>
            </div>
            <div class="goods-type">
                <el-tabs @tab-click="getfoods">
                    <el-tab-pane label="餐桌">
                        <ul class="cookList">
                            <li v-for="desk in deskData" :key="desk.deskId" @click="addOrderList(desk)">
                                <span class="foodImg"><img :src="deskurl" width="100%"></span>
                                <span class="foodName">{{desk.tableId}}号桌</span>
                                <span class="foodName" v-if="desk.status==1">可用</span>
                                <span class="foodName" v-else>不可用</span>
                            </li>
                        </ul>
                    </el-tab-pane>

                    <el-tab-pane v-for="item in foodtype" :key="item.goodsCategoryId" :label="item.category">
                        <ul class="cookList">
                            <li v-for="goods in typeGoods" :key="goods.name" @click="addOrderList(goods)">
                                <span class="foodImg"><img :src="goods.pictureUrl" height="60px" width="60px"></span>
                                <span class="foodName">{{goods.name}}</span>
                                <span class="foodPrice">￥{{goods.price}}元</span>
                            </li>
                        </ul>
                    </el-tab-pane>
                </el-tabs>
            </div>
        </el-col>
    </el-row>
</div>
</template>

<script>
import axios from 'axios'
import {
    queryTables
} from '../api/desk'
import {
    checkOrder,
    checkOut
} from '../api/order'
import {
    queryGoodsById,
    queryGoodsCategory
} from '../api/goods'
import {
    queryVipById
} from '../api/vip'
import {
    log
} from 'util';
import { parse } from 'path';

export default {
    name: 'pos',
    data() {
        return {
            isVip: false,
            cut:1,
            input: '',
            allPrice: '',
            deskID: '',
            vipId: -1,
            deskurl: 'https://f10.baidu.com/it/u=860792909,523430724&fm=72',
            deskInfo: '',
            isClear: false,
            foodtype: '',
            tableData: [], // 订单列表
            oftenGoods: [], // 常用商品数据
            noPayData: [], //挂单桌号及其消费
            typeGoods: [],
            deskData: [{
                tableId: '1',
                deskImg: 'https://f10.baidu.com/it/u=860792909,523430724&fm=72',
                status: '可用'
            }, ],
        }
    },
    methods: {
        checkVip(input) {
            console.log("会员号为" + input)
            this.isVip = true;
            if (this.isVip) {
                queryVipById(this.input).then((res) => {
                    this.input = '';
                    if (res.data.status == 1) {
                        this.isVip = true
                        this.vipId=res.data.result.vipId
                         this.cut=0.8
                        alert(res.data.result.name)
                        
                    }
                })
            }
        },
        addOrderList(goods) {
            let isHave = false
            let isDesk = false
            //  判断是否为桌子序号
            for (let g of this.deskData) {
                if (g.tableId == goods.tableId) {
                    isDesk = true
                    break
                }
            }
            // 判断属否已经存在于订单列表中
            for (let g of this.tableData) {
                if (g.name == goods.name) {
                    isHave = true
                    break
                }
            }
            if (isDesk) {
                if (goods.status == '不可用') {
                    this.deskID = goods.tableId
                    this.deskInfo = this.deskID + "号桌正在用餐！"
                    this.isClear = true
                } else {
                    this.deskID = goods.tableId,
                        this.deskInfo = '请到' + this.deskID + '号桌用餐'
                    this.isClear = false
                }
            }
            if (isHave) {
                let arr = this.tableData.filter(o => o.name == goods.name)
                console.log(goods.name)
                arr[0].count++
                arr[0].price += goods.price
            } else {
                if (!isDesk) {
                    let newGoods = {
                        goodsId: goods.goodsId,
                        name: goods.name,
                        price: goods.price,
                        count: 1
                    }
                    this.tableData.push(newGoods)
                }
            }
            if (this.tableData) {
                this.totalCount = 0
                this.totalPrice = 0
                for (let o of this.tableData) {
                    this.totalCount += o.count
                    this.totalPrice += o.price * o.count
                }
                if (this.isVip) {
                    this.allPrice = this.totalPrice * 0.8
                }
                this.allPrice = this.totalPrice
            }
        },
        delOrderList(goods) {
            this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId)
        },
        clearDesk(deskID) {
            console.log(deskID + "号桌正在清理")
            for (let d of this.deskData) {
                if (d.tableId == deskID) {
                    d.status = "可用"
                    this.isClear = false
                    this.deskInfo = '请到' + d.tableId + '号桌用餐'
                }
            }
        },
        //挂单数据
        addNoPayData(deskid, allprice) {
            console.log(deskid + "号桌共消费" + allprice);
            for (let d of this.deskData) {
                if (d.tableId == deskid) {
                    d.status = "不可用"
                    this.isClear = true
                    this.deskInfo = d.tableId + '号桌正在用餐！'
                }
            }
            console.log(this.tableData)

            // 判断属否已经存在于订单列表中
            let isHaveDesk = false;
            for (let d of this.noPayData) {
                if (d.tableId == deskid) {
                    isHaveDesk = true
                    break
                }
            }
            if (isHaveDesk) {
                let arr = this.noPayData.filter(o => o.tableId == deskid)
                arr[0].payPrice += allprice
            } else {
                let newCustomer = {
                    tableId: deskid,
                    payPrice: allprice,
                    details: this.tableData
                }
                //确定订单
                checkOrder(this.tableData).then((res) => {
                    if (res.data.tatusOrWaterNumId) {
                        this.tableData = []
                        alert(res.data.tatusOrWaterNumId)
                        newCustomer.tatusOrWaterNumId = res.data.tatusOrWaterNumId
                        console.log("流水号")
                        console.log(newCustomer)
                        this.noPayData.push(newCustomer)
                    }
                })

            }
        },
       
        //挂单结账
        checkoutFinal(desk) {
            console.log(desk.tableId + "正在结账，请稍候......")
            console.log("消费具体详情")
            console.log(desk.tatusOrWaterNumId)

            let waterNumId = desk.tatusOrWaterNumId
            let salesOrderId = this.$store.state.userinfo.employeeId //收营员ID
            let sumPrice = this.totalPrice //总价
            let discountSumPrice=this.totalPrice*this.cut //折后价

            let checkOutData = {
                "salesOrderId": salesOrderId,
                "vipId": this.vipId,
                "waterNumId": waterNumId,
                "sumPrice": sumPrice,
                "discountSumPrice": discountSumPrice
            }

            checkOut(checkOutData).then((res) => {
                if (res.data.status == 1) {
                    alert("结账成功")
                    this.noPayData.splice(desk, 1)
                    if (this.tableData.length) {
                        this.tableData = []
                        this.$message({
                            message: '结账成功'
                        })
                    } else {
                        this.$message.error('当前点餐为空，请选择正确的订单结账！')
                    }
                }
            })
        },
        getfoods(tab, event) {
            let type = parseInt(tab.index)
            console.log(type)
            if (type == 0) {
                console.log("正在向后台获取所有桌子...")
            } else {
                if (type <= this.foodtype.length) {
                    console.log("正在向后台获取类型为" + this.foodtype[type - 1].category + "的食品.....")
                    let queryType = this.foodtype[type - 1].goodsCategoryId
                    queryGoodsById(queryType).then((res) => {
                        if (res.data) {
                            this.typeGoods = res.data.goods
                        }
                    })
                }
            }

        },
    },
    created: function () {

        console.log("正在加载商品信息")
        // this.foodtype = this.$store.state.foodtype
        //查询所有桌子
        queryTables().then((res) => {
            if (res.data) {
                this.deskData = res.data.tables

            }
        })

        queryGoodsCategory().then((res) => {
            if (res.data) {
                this.foodtype = res.data.goodsCategory
            }
        })
    }
}
</script>

<style scoped>
.pos-row {
    height: 43vw;
}

.el-input {
    width: 78%;
}

.pos-order {
    background-color: honeydew;
    border-right: 1px solid #c0ccda;
    height: 100%;
}

.title {
    height: 20px;
    border-bottom: 1px solid #d3dce6;
    background-color: #f9fafc;
    padding: 10px;
}

.often-goods-list {
    overflow: hidden;
}

.often-goods-list ul li {
    list-style: none;
    float: left;
    border: 1px solid #e5e9f2;
    padding: 10px;
    margin: 5px;
    background-color: #fff;
}

.o-price {
    color: #58b7ff;
}

.cookList li {
    list-style: none;
    width: 15%;
    border: 1px solid #e5e9f2;
    height: auot;
    overflow: hidden;
    background-color: #fff;
    padding: 2px;
    float: left;
    margin: 2px;
}

.cookList li span {
    display: block;
    float: left;
}

.foodImg {
    width: 40%;
}

.foodName {
    font-size: 18px;
    padding-left: 10px;
    color: brown;
}

.foodPrice {
    font-size: 16px;
    padding-left: 10px;
    padding-top: 10px;
     color: brown;
}

.el-tabs__content {
    width: 100vw;
}
</style>



